            <div class="imgBox">
              <img src="" alt="">
            </div>
            <div class="mainTitle overflow2">
              40天财商读书会，让你摆脱房奴卡奴，学会躺着赚钱！
            </div>
            <div class="hotAndMoney clearfix">
              <span class="hot fl">4.4万人气</span>
