<style lang="less" scoped>
  #pageBox{
    .pageTitle{
      font-size:0.16rem;
      font-weight: bold;
      margin:10px 0;
    }
    .hotBox{
      .hotList{
        .hotItem{
          float:left;
          width:50%;
          margin-bottom:20px;
          &:nth-child(even){
            padding-left:0.05rem;
          }
          &:nth-child(odd){
